import React, { useState, useEffect, useRef } from 'react';
import MonacoEditor from '@monaco-editor/react';
import { marked } from "marked";
import DOMPurify from "dompurify";
import { handleClick, handleEditor } from './Handlers.tsx';
import buttons from './Buttons.tsx';

const LatexEditor = ({ closeEditor, saveEditor }) => {
  const [text, setText] = useState('');
  const editorRef = useRef(null); // Reference to the Monaco Editor instance

  useEffect(() => {
    window.renderMathInElement(document.getElementById(`renderArea`), {
      delimiters: [
        { left: '$$', right: '$$', display: true },
        { left: '$', right: '$', display: false },
        { left: '\\(', right: '\\)', display: false },
        { left: '\\[', right: '\\]', display: true },
      ],
      throwOnError: false,
    });
  }, [text]);

  const handleEditorChange = (value) => {
    setText(value);
  };

  return (
    <div id="latex-editor">

      <div style={{
        flex: '1',
        overflowY: 'auto',
        border: '1px solid #ccc',
        borderRadius: '4px',
        padding: '10px',
      }}>
        <div id="latex-editor-toolbar">
          {buttons.map((item, index) => (
            <button key={index} onClick={() => handleClick(editorRef.current, item.make, setText)}>
              {item.label}
            </button>
          ))}
        </div>

        <MonacoEditor
          height="700px"
          language="latex"
          theme="vs-dark"
          value={text}
          onChange={handleEditorChange}
          onMount={handleEditor(editorRef).handleEditorMount}
        />
      </div>


      <div
        style={{
          flex: '1',
          overflowY: 'auto',
          border: '1px solid #ccc',
          borderRadius: '4px',
          padding: '10px',
        }}>
          
        <button onClick={() => saveEditor(text)}>SAVE</button>
        <button onClick={closeEditor}>CANCEL</button>

        <p id="renderArea" dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(marked(text)) }} />


      </div>


    </div>
  );
};

export default LatexEditor;
