import moment from "moment";

const List = ({ data: tasks, deleteTask, finishTask, editTask }) => {
    return (

        <div>
            {tasks.map((item) => {
                let marker = (item.id > localStorage.getItem("lastSaved")) ? "*" : "";                
                return (
                    <div key={item.id}>{item.title}{marker}
                        <p></p>
                        <small>{(item.completed ? "Completed" : "Unfinished")}{`\n`}</small>
                        <p></p>
                        <small>{"Created: " + moment(item.id).fromNow()}{`\n`}</small>
                        <p></p>
                        <button onClick={() => {
                            let value = prompt("Edit Text: Enter New Value");
                            value ? editTask(item.id, value) : console.log("No Input")
                        }}>EDIT</button>&nbsp;
                        <button onClick={() => {
                              window.confirm("Confirm Delete?") && deleteTask(item.id)
                        }}>DELETE</button>&nbsp;
                        <button onClick={() => {}}>UP</button>&nbsp;
                        <p></p>
                        <hr />
                    </div>

                )
            })}
        </div>

    );
}

export default List;