import { useState, useEffect } from "react";
import "./App.css";
import List from "./List";
import { useHistory, useParams } from "react-router-dom";
import LatexEditor from "./LatexEditor";

const Todo = () => {
  const [text, setText] = useState('');
  const [tasks, setTask] = useState([]);
  const [isSaved, setIsSaved] = useState(true); // Track save state
  const history = useHistory();
  const [isEdit, setIsEdit] = useState(false);

  const deleteTask = (key) => {
    setTask(tasks.filter(task => task.id !== key));
    setIsSaved(false);
  };

  const editTask = (key, value) => {
    setTask(tasks.map(t => (key === t.id ? { ...t, title: value } : t)));
    setIsSaved(false);
  };

  const finishTask = (key) => {
    setTask(tasks.map(t => (key === t.id ? { ...t, completed: !t.completed } : t)));
    setIsSaved(false);
  };

  const localSave = () => {
    localStorage.setItem("lastSaved", Date.now())
    localStorage.setItem("tasks", JSON.stringify(tasks))
    console.log(JSON.stringify(localStorage))
    setIsSaved(true);
  };

  const closeEditor = () => {

    setText("");
    setIsSaved(false);
    setIsEdit(false);
  }

  const saveEditor = (latex) => {
    setTask([
      ...tasks,
      {
        userId: 1,
        id: Date.now(),
        title: latex,
        completed: false,
      },
    ]);
    setText("");
    setIsSaved(false);
    setIsEdit(false);
  }

  useEffect(() => {
    setTask(JSON.parse(localStorage.getItem("tasks") ?? "[]"));

  }, []);

  useEffect(() => {
    window.renderMathInElement(document.body, {
      delimiters: [
        { left: '$$', right: '$$', display: true },
        { left: '$', right: '$', display: false },
        { left: '\\(', right: '\\)', display: false },
        { left: '\\[', right: '\\]', display: true }
      ],
      throwOnError: false,
    });
  }, [text, tasks, isEdit, isSaved]);

  useEffect(() => {
    const handleBeforeUnload = (event) => {
      if (!isSaved) {
        event.preventDefault();
        event.returnValue = ""; // Chrome requires this for the dialog
      }
    };

    window.addEventListener("beforeunload", handleBeforeUnload);

    return () => {
      window.removeEventListener("beforeunload", handleBeforeUnload);
    };
  }, [isSaved, text]);

  return (

    <>
    <div style={{
  backgroundColor: '#333', 
  paddingTop: '5px',
  paddingBottom: '5px', 
  position: 'sticky', 
  top: '0', 
  left: '0', 
  right: '0', 
  zIndex: '1000', 
  width: '100%'
}}>
        <div style={{ display: 'flex', justifyContent: 'space-around' }}>
          <div><a href="/" style={{ color: 'white', textDecoration: 'none' }}>Home</a></div>
          <div><a href="/about" style={{ color: 'white', textDecoration: 'none' }}>About</a></div>
          <div><a href="/services" style={{ color: 'white', textDecoration: 'none' }}>Services</a></div>
          <div><a href="/contact" style={{ color: 'white', textDecoration: 'none' }}>Contact</a></div>
        </div>
      </div>
      {!isEdit &&

        <div id="container">
          
          <div id="content" style={{
            flex: '2',
            overflowY: 'auto',
            border: '1px solid #ccc',
            borderRadius: '4px',
            padding: '10px',
          }}>
            <button onClick={localSave}> SAVE </button>

            <h1>Title</h1>

            <p>{text}</p>

            <List
              data={tasks}
              deleteTask={deleteTask}
              finishTask={finishTask}
              editTask={editTask}
            />


            <button onClick={() => {
              setIsEdit(true);
              window.scrollTo(0, 0);
            }}>Add Step</button>

          </div>

          <div id="sidebar" style={{
            flex: '1',
            overflowY: 'auto',
            //border: '1px solid #ccc',
            borderRadius: '4px',
            padding: '10px',
          }}>
            <p style={{position: 'fixed'}}>shfjsfsf</p>
          </div>
        </div>}

      {isEdit && <LatexEditor closeEditor={closeEditor} saveEditor={saveEditor} />}


    </>

  );
};

export default Todo;
