const handleClick = (ref, make, callback) => {
    if (ref) {
        const editor = ref;

        // Get the currednt cursor position
        const position = editor.getPosition();

        // Insert the "make" text at the current cursor position
        editor.executeEdits(null, [
            {
                range: {
                    startLineNumber: position.lineNumber,
                    startColumn: position.column,
                    endLineNumber: position.lineNumber,
                    endColumn: position.column,
                },
                text: make,
                forceMoveMarkers: true,
            },
        ]);

        callback(editor.getValue());
        editor.focus();
    }
};

const handleEditor = (ref) => {
    return {
        handleEditorMount: (editor, monaco) => {
            ref.current = editor; // Save the editor instance

            // Register LaTeX language when Monaco is mounted
            monaco.languages.register({ id: 'latex' });

            monaco.languages.setMonarchTokensProvider('latex', {
                tokenizer: {
                    root: [
                        [/[{}]/, 'delimiter.curly'],
                        [/\$[^\$]*\$/, 'string.math'],
                        [/[\\][a-zA-Z]+/, 'keyword'],
                        [/[a-zA-Z]+/, 'identifier'],
                        [/[0-9]+/, 'number'],
                        [/&/, 'operator'], // Add this rule for the `&` token
                    ],
                },
            });

            monaco.languages.setLanguageConfiguration('latex', {
                brackets: [
                    ['{', '}'],
                    ['$', '$'],
                ],
                autoClosingPairs: [
                    { open: '{', close: '}' },
                    { open: '$', close: '$' },
                ],
            });
        },
    };
}

export { handleClick, handleEditor };